/**
 * ë¬¸ìžì—´ì„ ì•ˆì „í•˜ê²Œ ì •ë¦¬í•©ë‹ˆë‹¤
 */
export const sanitizeString = (
  str: string | undefined,
  maxLength = 300,
): string => {
  if (!str) return ''

  return str
    .replace(/[<>]/g, '') // ê¸°ë³¸ì ì¸ HTML íƒœê·¸ ì œê±°
    .replace(/javascript:/gi, '') // javascript: í”„ë¡œí† ì½œ ì œê±°
    .replace(/on\w+\s*=/gi, '') // ì´ë²¤íŠ¸ í•¸ë“¤ëŸ¬ ì œê±°
    .trim()
    .slice(0, maxLength) // ìµœëŒ€ ê¸¸ì´ ì œí•œ
}

/**
 * URLì„ ì•ˆì „í•˜ê²Œ ì •ë¦¬í•©ë‹ˆë‹¤
 */
export const sanitizeUrl = (url: string | undefined): string => {
  if (!url) return ''

  // URL ê²€ì¦: http, https, ìƒëŒ€ê²½ë¡œë§Œ í—ˆìš©
  if (
    url.startsWith('http://') ||
    url.startsWith('https://') ||
    url.startsWith('/')
  ) {
    return url.replace(/[<>"']/g, '') // ìœ„í—˜í•œ ë¬¸ìž ì œê±°
  }

  return ''
}

/**
 * HTML íƒœê·¸ë¥¼ ì™„ì „ížˆ ì œê±°í•©ë‹ˆë‹¤
 */
export const stripHtmlTags = (html: string): string => {
  return html.replace(/<[^>]*>/g, '')
}

/**
 * ì´ë©”ì¼ ì£¼ì†Œê°€ ìœ íš¨í•œì§€ ê²€ì¦í•©ë‹ˆë‹¤
 */
export const isValidEmail = (email: string): boolean => {
  const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
  return emailRegex.test(email)
}

/**
 * ìŠ¬ëŸ¬ê·¸ê°€ ìœ íš¨í•œì§€ ê²€ì¦í•©ë‹ˆë‹¤
 */
export const isValidSlug = (slug: string): boolean => {
  const slugRegex = /^[a-z0-9-]+$/
  return slugRegex.test(slug)
}
